* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Hide scrollbar for all browsers while maintaining scroll functionality */
html {
  scroll-behavior: smooth;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
}

/* Hide scrollbar for Chrome, Safari and Opera */
html::-webkit-scrollbar {
  display: none;
}

body {
  overflow-x: hidden;
  background-color: #000;
  font-family: 'Arial', sans-serif;
  position: relative;
}

/* Global cool gradient background */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(125deg, rgba(0, 8, 20, 0.9), rgba(0, 20, 40, 0.8)),
    radial-gradient(circle at 20% 30%, rgba(0, 140, 255, 0.15) 0%, transparent 40%),
    radial-gradient(circle at 80% 20%, rgba(0, 255, 200, 0.15) 0%, transparent 40%),
    radial-gradient(circle at 10% 80%, rgba(100, 0, 255, 0.15) 0%, transparent 40%),
    radial-gradient(circle at 90% 90%, rgba(255, 50, 150, 0.15) 0%, transparent 40%);
  background-attachment: fixed;
  z-index: -1;
  pointer-events: none;
}

.App {
  width: 100%;
  position: relative;
  overflow-x: hidden;
}

.parallax-container {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.parallax-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  transition: transform 0.05s ease-out;
  will-change: transform;
  object-fit: cover;
  display: block;
}

/* Layer-specific customizations */
.layer-7 {
  object-position: center;
}

/* Scroll Down Button Styles */
.scroll-down-container {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.scroll-down-button {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 30px;
  color: white;
  padding: 12px 24px;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.3s ease;
  backdrop-filter: blur(5px);
}

.scroll-down-button:hover {
  background: rgba(0, 0, 0, 0.7);
  transform: translateY(-5px);
}

.scroll-down-arrow {
  margin-top: 5px;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* Smooth transition between sections */
#next-section {
  scroll-margin-top: 0;
  transition: all 0.5s ease-in-out;
}

/* Media queries for responsive design */
@media screen and (max-width: 768px) {
  .parallax-layer {
    width: auto;
    height: 100vh;
    left: 50%;
    /* Transform is handled in JS for animated layers */
  }
  
  /* For non-animated layers */
  .layer-1, .layer-2, .layer-3, .layer-8, .layer-9 {
    transform: translateX(-50%);
  }
  
  /* Specific layer adjustments for mobile */
  .layer-7 {
    min-height: 100vh;
    min-width: 120%; /* Ensure it's wider than the viewport */
  }
}

@media screen and (max-width: 480px) {
  .parallax-layer {
    height: 100vh;
    width: auto;
    min-width: 100%;
    max-width: none;
    left: 50%;
    /* Transform handled in JS for animated layers */
  }
  
  /* For non-animated layers */
  .layer-1, .layer-2, .layer-3, .layer-8, .layer-9 {
    transform: translateX(-50%);
  }
  
  /* Make some layers wider on mobile to ensure coverage */
  .layer-4, .layer-5, .layer-6, .layer-7 {
    min-width: 150%; /* Ensure it covers the screen even with animations */
  }
}
