@font-face {
  font-family: 'TheBoldFont';
  src: url('./assets/font/THEBOLDFONT-FREEVERSION.woff') format('woff'),
       url('./assets/font/THEBOLDFONT-FREEVERSION.ttf') format('truetype'),
       url('./assets/font/THEBOLDFONT-FREEVERSION.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/* Apply the font to all elements by default */
* {
  font-family: 'TheBoldFont', sans-serif;
}

h1, h2, h3, h4, h5, h6, p, button, span, div {
  font-family: 'TheBoldFont', sans-serif;
}
